body {
  font-family: system-ui, sans-serif;
  background-color: #ffffff;
  color: #000000;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.animate-fadeIn {
  animation: fadeIn 1.5s ease-in-out;
}

